.header_section {
    // padding-top: 40px
}

.header_section nav {
    display: flex;
    justify-content: space-between;
    padding: 40px 18px;
    align-items: center
}

@media (max-width: 30rem) {
    .header_section nav {
        justify-content:center;
        flex-wrap: wrap;
        padding: 20px 18px
    }
}

.header_section nav .brand {
    padding: 6px 0
}

.header_section nav .brand img {
    height: 30px;
    transition: all .25s cubic-bezier(.02,.01,.47,1)
}

.header_section nav .brand:hover {
    background: #000
}

.header_section nav .brand:hover img {
    -webkit-filter: invert(1);
    filter: invert(1)
}

.header_section nav .menu {
    display: flex;
    justify-content: space-between;
    vertical-align: middle;
    align-items: center
}

@media (max-width: 30rem) {
    .header_section nav .menu {
        justify-content:center;
        flex-wrap: wrap;
        margin-top: 20px
    }
}

.header_section nav .menu ul {
    padding: 0;
    font-size: 0;
    display: flex;
    justify-content: center
}

.header_section nav .menu ul>li {
    font-size: 20px;
    display: list-item
}

.header_section nav .menu ul>li a {
    display: block
}

.header_section nav .menu ul .active {
    font-weight: 600
}

.header_section nav .menu .menu-links li {
    line-height: normal;
    margin: 0 22px
}

.header_section nav .menu .menu-links li a {
    line-height: normal
}

@media (max-width: 30rem) {
    .header_section nav .menu .menu-social {
        margin-top:20px
    }
}

.header_section nav .menu .menu-social li a {
    line-height: 0;
    margin-left: 12px;
    padding: 7px
}

.header_section nav .menu .menu-social li a img {
    height: 18px
}

.header_section nav .menu .menu-social li a:hover img {
    -webkit-filter: invert(1);
    filter: invert(1)
}

