.typewriter {
    overflow: hidden;
    border-right: 5px solid black;
    white-space: nowrap;
    // margin: 0 auto;
    width: 3.5em;
    animation: 
      typing 3.5s steps(40, end),
      blink-caret .75s step-end infinite;
  }
  
  /* The typing effect */
  @keyframes typing {
    from { width: 0 }
    to { width: 3.5em }
  }
  
  /* The typewriter cursor effect */
  @keyframes blink-caret {
    from, to { border-color: transparent }
    50% { border-color: black; }
  }